<template>
	<div id="blogpost-page">
		<div class="blogpost container">
			<div class="blogpost-wrapper">
				<div class="blogpost-head p-3">
					<div class="blogpost-content">
						<p class="blogpost-breadcrumb">
							<router-link :to="{ name: 'Home' }" class="text-white">Home</router-link>
							<i class="fa fa-chevron-right mx-2"></i>
							<router-link :to="{ name: 'Blog' }" class="text-white">Blog</router-link>
							<i class="fa fa-chevron-right mx-2"></i>
							{{post.title}}
						</p>
						<h2 class="blogpost-post-title">{{post.title}}</h2>
					</div>
					<div class="blogpost-bg">
						<div style="position: relative;width: 100%">
							<img src="../assets/comp/dotdot.svg" style="top:30px;right: 0" class="fadeIn img11">
							<img src="../assets/comp/trapehead.svg" style="right: 0;" class="enterFromRight img12">
						</div>
					</div>
				</div>
				<div class="blogpost-info">
					<div class="row">
						<div class="col-md-6">
							<small>Posted on {{post.datetime}}</small><br />
							<small>Tags: {{post.tag}}</small>
						</div>
						<div class="col-md-6">
							<small>Viewed 123 times</small>
							
						</div>
						
					</div>
				</div>
				
				<vue-markdown :source="post.content" class="vue-markdown">
				</vue-markdown>
				<div v-if="is_loaded" class="blogpost-footer row mt-5">
					<div class="col-md-6">
						<router-link v-if="pagination.before" :to="{ name: 'BlogPost', params: { slug: pagination.before.slug } }">
							<i class="fas fa-chevron-left mr-2"></i>{{pagination.before.title}}
						</router-link>
						<span v-else>This is first post</span>
					</div>
					<div class="col-md-6 text-right">
						<router-link v-if="pagination.next" :to="{ name: 'BlogPost', params: { slug: pagination.next.slug } }">
							{{pagination.next.title}}<i class="fas fa-chevron-right ml-2"></i>
						</router-link>
						<span v-else>This is last post</span>
					</div>
				</div>

				
			</div>
		</div>

		<p class="text-center mt-5">
			<small>Created By Putu Jhonarendra. Pageviews: {{pageview.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ".")}}</small>
		</p>
		<ButtonToTop />
		<div class="pb-5"></div>
	</div>
</template>

<script>
import axios from 'axios'
import firebase from './../services/firebase'
import viewCounter from './../mixins/viewCounter'
import VueMarkdown from 'vue-markdown'
import ButtonToTop from '../components/ButtonToTop'
import '../assets/css/blogpost.css'
export default {
	data(){
		return {
			blog_data: [],
			post: {},
			pagination: {
				before: {},
				next: {}
			},
			is_loaded: false
		}
	},
	mixins: [viewCounter],
	components: { VueMarkdown, ButtonToTop },
	methods: {
		showPost(){
			if(navigator.onLine){
				const db = firebase.firestore()
				db.collection('blog')
				.get()
				.then((q) => {
					let data = []
					q.forEach((doc) => {
						data.push(doc.data())
					})
					this.blog_data = data

					this.blog_data.sort((a, b)=>{
						let dateA = new Date(a.datetime.toDate().toString())
						let dateB = new Date(b.datetime.toDate().toString())
						return dateA - dateB
					})

					let slug = this.$route.params.slug
					let show_post

					this.blog_data.forEach((e) => {
						if(slug == e.slug){
							show_post = e
							show_post.datetime = e.datetime.toDate().toString().substr(0, 15)

							// get md file post content
							axios
							.get('/blog/'+e.file)
							.then(res => {
								show_post.content = JSON.parse(JSON.stringify(res.data))
								this.post = show_post
								this.setPagination()
							})
						}
					})
				})
				.catch((error) => {
					console.error("Error getting documents: ", error)
				})
			} else {
				axios
				.get('/blog/sitemap.json')
				.then(res => {
					this.blog_data = JSON.parse(JSON.stringify(res.data))

					let slug = this.$route.params.slug
					let show_post

					this.blog_data.forEach((e) => {
						if(slug == e.slug){
							show_post = e
							// show_post.datetime = e.datetime.toDate().toString()

							// get md file post content
							axios
							.get('/blog/'+e.file)
							.then(res => {
								show_post.content = JSON.parse(JSON.stringify(res.data))
								this.post = show_post
								this.setPagination()
							})
						}
					})
				})
			}
			
		},
		setPagination(){
			let post = this.post
			let i = 0
			let cur_index = 0 // current post index
			this.blog_data.forEach(function(e){
				// get current index
				if(post.slug == e.slug){
					cur_index = i
				}
				i += 1
			})
			this.pagination.before = this.blog_data[cur_index-1]
			this.pagination.next = this.blog_data[cur_index+1]
			this.is_loaded = true;
		}
	},
	mounted(){
		// this.updatePageview() // call mixins
		this.showPost()
		window.scroll({
			top: 0,
			behavior: 'smooth'
		})
	}
}
</script>